/* src/components/About.css */

.about {
  background: linear-gradient(145deg, #1e1e2f, #252545);
  color: var(--text-color);
  padding: 50px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.3s;
}

.about-content {
  display: flex;
  align-items: center;
  max-width: 1200px;
  gap: 50px;
  flex-direction: row;
}

.about-text {
  flex: 1;
}

.about-text h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: var(--primary-color);
  font-family: 'Courier New', Courier, monospace;
}

.about-text p {
  font-size: 1.1em;
  line-height: 1.8;
  margin-bottom: 20px;
  color: #bbb;
}

.resume-button {
  display: inline-flex;
  align-items: center;
  background: linear-gradient(145deg, var(--primary-color), var(--secondary-color));
  color: var(--text-color);
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 5px;
  font-size: 1em;
  margin-top: 20px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
  font-weight: bold;
}

.resume-button i.icon {
  margin-right: 10px;
  font-size: 1.2em;
}

.resume-button:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
}

.about-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.about-image img {
  width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: scale(1.05);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  .about-text {
    text-align: center;
  }

  .about-text h2 {
    font-size: 2em;
  }

  .about-text p {
    font-size: 1em;
  }

  .resume-button {
    width: 100%;
    max-width: 250px;
    justify-content: center;
    margin: 0 auto;
  }

  .about-image img {
    width: 80%;
    margin: 0 auto;
  }
}
