/* src/components/Projects.css */

.projects {
  background: linear-gradient(145deg, #252545, #333);
  color: var(--text-color);
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: background 0.3s;
}

.projects-heading {
  text-align: center;
  margin-bottom: 20px;
}

.projects-heading h2 {
  font-size: 3em;
  color: var(--primary-color);
  font-family: 'Courier New', Courier, monospace;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.divider {
  width: 150px;
  height: 2px;
  background-color: var(--primary-color);
  margin: 10px auto 20px;
  border: none;
}

.projects-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  width: 100%;
  max-width: 1200px;
}

.project-card {
  background: #292938;
  border-radius: 15px;
  padding: 0;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.5);
}

.project-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 15px 15px 0 0;
}

.project-info {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-info h3 {
  font-size: 1.8em;
  margin: 10px 0;
  color: var(--primary-color);
  text-transform: capitalize;
  font-weight: 600;
  font-family: 'Courier New', Courier, monospace;
}

.project-info p {
  font-size: 1.1em;
  color: #ccc;
  margin-bottom: 20px;
}

.live-view {
  background: var(--primary-color);
  color: var(--text-color);
  padding: 12px 20px;
  text-decoration: none;
  border-radius: 30px;
  font-size: 1.1em;
  font-weight: 600;
  font-family: 'Courier New', Courier, monospace;
  transition: background 0.3s, transform 0.3s, box-shadow 0.3s;
  align-self: flex-start;
}

.live-view:hover {
  background: var(--secondary-color);
  transform: scale(1.05);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.4);
}

/* Responsive Styles */
@media screen and (max-width: 1024px) {
  .projects-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media screen and (max-width: 768px) {
  .projects-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  .project-image {
    height: 250px;
  }
}

@media screen and (max-width: 480px) {
  .projects-list {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .projects-heading h2 {
    font-size: 2em;
  }

  .project-info h3 {
    font-size: 1.5em;
  }

  .project-info p {
    font-size: 1em;
  }

  .live-view {
    padding: 10px 15px;
    font-size: 1em;
  }

  .project-image {
    height: 250px;
  }
}
