/* src/components/Home.css */

.home {
  background: linear-gradient(145deg, #1e1e2f, #1b1b29);
  color: var(--text-color);
  padding: 70px 10px;
  text-align: center;
  position: relative;
  transition: background 0.3s;
}

.home h1 {
  font-size: 4em;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: 'Courier New', Courier, monospace;
}

.home h1 .function {
  color: var(--primary-color);
}

.home h1 .name {
  color: var(--primary-color);
}

.home h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  font-weight: bold;
}

.description {
  font-size: 1.2em;
  line-height: 1.8;
  max-width: 800px;
  margin: 0 auto 20px;
  color: #bbb;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.cta {
  background: linear-gradient(145deg, var(--primary-color), var(--secondary-color));
  border: none;
  padding: 15px 30px;
  color: var(--text-color);
  cursor: pointer;
  font-size: 1.2em;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.cta:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
}

.settings {
  background: transparent;
  border: 2px solid var(--primary-color);
  padding: 15px;
  color: var(--primary-color);
  font-size: 1.2em;
  cursor: pointer;
  border-radius: 50%;
  transition: transform 0.2s, border-color 0.2s;
}

.settings:hover {
  transform: rotate(20deg);
  border-color: var(--secondary-color);
}

.gear-icon {
  font-size: 1.5em;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .home {
    padding: 50px 15px;
  }

  .home h1 {
    font-size: 2.5em;
  }

  .home h2 {
    font-size: 1.8em;
  }

  .description {
    font-size: 1em;
    line-height: 1.6;
    max-width: 100%;
    padding: 0 10px;
  }

  .buttons {
    flex-direction: column;
    gap: 15px;
  }

  .cta, .settings {
    width: 100%;
    max-width: 250px;
  }
}
