/* src/components/Footer.css */

.footer {
  background: linear-gradient(145deg, var(--footer-background), #252545);
  color: var(--text-color);
  padding: 100px 90px;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
  box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: background 0.3s;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.footer-text h3{
  font-size: 2.2em;
  color: #ccc;
  margin-bottom: 20px;
}

.footer-text p {
  font-size: 2.2em;
  color: var(--primary-color);
  margin-bottom: 20px;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.social-link {
  margin: 0 15px;
  color: var(--primary-color);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.social-link:hover {
  color: #fff;
}

.social-link svg {
  transition: transform 0.3s ease-in-out;
}

.social-link:hover svg {
  transform: scale(1.2);
}

.footer-profile {
  margin-top: 10px;
}

.footer-profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid var(--primary-color);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
}

.footer-profile-pic:hover {
  transform: scale(1.1);
}

.footer p {
  margin: 5px 0;
}

.dark-mode-toggle {
  background: transparent;
  border: none;
  color: var(--primary-color);
  cursor: pointer;
  font-size: 1.5em;
  margin-top: 20px;
  transition: transform 0.3s;
}

.dark-mode-toggle:hover {
  transform: rotate(20deg);
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .footer-content {
    padding: 20px;
  }

  .footer-text {
    font-size: 1em;
  }

  .social-link {
    margin: 0 8px;
  }

  .footer-profile-pic {
    width: 80px;
    height: 80px;
  }
}

@media screen and (max-width: 480px) {
  .footer-text {
    font-size: 0.9em;
  }

  .footer-profile {
    margin: 10px 0;
  }

  .social-link svg {
    width: 25px;
    height: 25px;
  }

  .dark-mode-toggle {
    font-size: 1.2em;
  }
}
